/* eslint-disable camelcase */
import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import router from './router'
import UpdateDate from './plugins/updateDate'

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    order: '',
    user: '',
    token: '',
    calcuOrder: '',
    predict: ''
  },
  getters: {
    getOrderData (state) {
      return state.order
    },
    getUserData (state) {
      return state.user
    },
    getTokenData (state) {
      return state.token
    },
    getCalcuOrderData (state) {
      return state.calcuOrder
    },
    getPredictData (state) {
      return state.predict
    }
  },
  mutations: {
    mu_getOrder (state, payload) {
      state.order = payload
    },
    mu_getUser (state, payload) {
      state.user = payload
    },
    mu_getToken (state, payload) {
      state.token = payload
    },
    mu_getCalculate (state, payload) {
      state.calcuOrder = payload
    },
    mu_getPredict (state, payload) {
      state.predict = payload
    }
  },
  actions: {
    ac_login (context, payload) {
      Vue.$cookies.set('pcwi', payload.uid)
      Axios.post('https://bagcw.com/auth_patner/login', payload).then((data) => {
        context.commit('mu_getToken', data.data.token)
        Vue.$cookies.set('jwtToken', data.data.token)
        alert('로그인 성공')
        router.push({
          name: 'home'
        })
      }).catch(e => {
        console.log(e)
        alert('로그인 실패')
      })
    },
    ac_getOrder (context, payload) {
      Axios.get('https://bagcw.com/auth_patner/order/' + payload).then((res) => {
        let updateDate = new UpdateDate()
        let data = updateDate.getUpdateDate(res.data)
        var jungbok = data.reduce(function (accumulator, current) {
          if (checkIfAlreadyExist(current)) {
            current.jungbok = 'bg'
            return accumulator.concat([current])
          } else {
            return accumulator.concat([current])
          }
          function checkIfAlreadyExist (currentVal) {
            return accumulator.some((item) => {
              if (item.product_code === currentVal.product_code && item.su === currentVal.su && item.address === currentVal.address && item.date === current.date) {
                item.jungbok = 'bg'
              }
              return (item.product_code === currentVal.product_code && item.su === currentVal.su && item.address === currentVal.address && item.date === current.date)
            })
          }
        }, [])
        context.commit('mu_getOrder', jungbok)
      })
    },
    ac_getPredict (context, payload) {
      Axios.get('https://bagcw.com/auth_patner/predict').then((res) => {
        context.commit('mu_getPredict', res.data)
      })
    },
    async ac_getCalculate (context, payload) {
      const { data: { user: { nongga_product, conditinal_free } } } = await Axios.get('https://bagcw.com/auth_patner')

      const products = JSON.parse(nongga_product)
      Axios.get('https://bagcw.com/auth_patner/order/' + payload.startDate + '/' + payload.endDate).then((res) => {
        let updateDate = new UpdateDate()
        let data = updateDate.getUpdateDate(res.data)
        var deleveryEditData = data.reduce(function (accumulator, current, index) {
          products.forEach((product) => {
            if (current.cw_code === product.code) {
              current.delivery_pay = product.deliveryPrice
              if (conditinal_free) {
                if (conditinal_free <= current.gong_price) {
                  current.delivery_pay = 0
                }
              }
            }
          })
          if (checkIfAlreadyExist(current)) {
            current.jungbok = 'bg'
            current.delivery_pay = 0
            return accumulator.concat([current])
          } else {
            return accumulator.concat([current])
          }
          function checkIfAlreadyExist (currentVal) {
            return accumulator.some((item) => {
              if (item.product_code === currentVal.product_code && item.su === currentVal.su && item.address === currentVal.address && item.date === current.date) {
                item.jungbok = 'bg'
              }
              return (item.product_code === currentVal.product_code && item.su === currentVal.su && item.address === currentVal.address && item.date === current.date)
            })
          }
        }, [])
        context.commit('mu_getCalculate', deleveryEditData)
      })
    },
    ac_updateuser (context, payload) {
      if (payload.pw !== payload.rePw) {
        alert('비밀번호가 일치하지 않습니다')
      } else {
        Axios.post('https://bagcw.com/auth_patner/update', payload).then((res) => {
        })
      }
    }
  }
})
