<template>
<v-content>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4>
        <v-card class="elevation-12">
          <v-toolbar dark color="primary">
            <v-toolbar-title>로그인</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field v-model="login.uid" prepend-icon="person" name="uid" label="아이디" type="text"></v-text-field>
              <v-text-field v-model="login.pw" prepend-icon="lock" name="password" label="비밀번호" id="password" type="password"></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="ac_login(login)">Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</v-content>
</template>

<script>
import { mapActions } from 'vuex'
import Vue from 'vue'
export default {
  created () {
    if (Vue.$cookies.get('pcwi')) {
      this.login.uid = Vue.$cookies.get('pcwi')
    }
  },
  data () {
    return {
      login: {
        uid: '',
        pw: ''
      }
    }
  },

  computed: {

  },

  methods: {
    ...mapActions([
      'ac_login'
    ])
  }
}
</script>
<style>
.h1 {
  text-align: center;
}
