import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Axios from 'axios'
import Store from './store'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/about',
      name: 'about',
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/order',
      name: 'order',
      component: () => import(/* webpackChunkName: "about" */ './views/Order.vue'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/simple-order',
      name: 'simple-order',
      component: () => import(/* webpackChunkName: "about" */ './views/SimpleOrder.vue'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/calculate',
      name: 'calculate',
      component: () => import(/* webpackChunkName: "about" */ './views/Calculate.vue'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/info',
      name: 'myinfo',
      component: () => import(/* webpackChunkName: "about" */ './views/Myinfo.vue'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/predict',
      name: 'predict',
      component: () => import(/* webpackChunkName: "about" */ './views/Predict.vue'),
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    }
  ]
})

router.beforeEach((to, from, next) => {
  let requireAuth = to.matched.some(record => record.meta.requireAuth)
  if (requireAuth) {
    Axios.defaults.headers.common['Authorization'] = Vue.$cookies.get('jwtToken')
    console.log(Store.getters.getTokenData)
    Axios.get('https://bagcw.com/auth_patner').then((data) => {
      Store.commit('mu_getUser', data.data.user)
      next()
    }).catch(function (error) {
      console.error(error)
      next('login')
    })
  } else {
    next()
  }
})

export default router
