<template>
  <v-app>
    <v-navigation-drawer
      v-if="drawer.visible"
      :clipped="drawer.clipped"
      :fixed="drawer.fixed"
      v-model="drawer.open"
      app
    >
      <v-list>
        <v-list>
          <v-list-tile>
            <v-list-tile-title class="title">{{getUserData.nongga_name}}님 환영합니다</v-list-tile-title>
          </v-list-tile>
        </v-list>
        <v-divider></v-divider>
        <v-list-tile to="/">
          <v-list-tile-action>
            <v-icon>dashboard</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>전체보기</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile to="/info">
          <v-list-tile-action>
            <v-icon>account_circle</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>내정보</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar
      v-if="toolbar.visible"
      app
      :fixed="toolbar.fixed"
      :clipped-left="toolbar.clippedLeft"
    >
      <v-toolbar-side-icon @click.stop="toggleDrawer"></v-toolbar-side-icon>
      <v-toolbar-title><router-link to="/">창원아재들 거래처 프로그램</router-link></v-toolbar-title>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat align-right @click="logout">로그아웃</v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-content>
      <v-container fluid fill-height>
        <v-layout justify-left align-left>
          <v-flex>
            <router-view></router-view>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
    <v-footer app :fixed="footer.fixed" :clipped-left="footer.clippedLeft">
      <span class="caption mx-3">&copy; 2018, MIT LICENSE - Free to use and learn from</span>
    </v-footer>
  </v-app>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'App',
  created () {
    let routerName = this.$router.history.current.name
    if (routerName === 'login') {
      this.drawer.visible = false
      this.toolbar.visible = false
    }
  },
  data () {
    return {
      drawer: {
        open: true,
        clipped: true,
        fixed: false,
        permanent: true,
        visible: true
      },
      toolbar: {
        fixed: true,
        clippedLeft: true,
        visible: true
      },
      footer: {
        fixed: true,
        clippedLeft: true
      }
    }
  },
  computed: {
    ...mapGetters([
      'getUserData',
      'getTokenData'
    ])
  },
  methods: {
    toggleDrawer () {
      // normal drawer
      this.drawer.open = !this.drawer.open
    },
    logout () {
      if (this.$cookies.get('jwtToken')) {
        this.$store.commit('mu_getToken', '')
        this.$cookies.remove('jwtToken')
        alert('로그아웃 완료')
        this.$router.push('login')
      }
    }
  }
}
</script>
