export default class updateDate {
  constructor () {
    this.updateData = []
  }

  getUpdateDate (data) {
    for (let i = 0; i < data.length; i++) {
      this.updateData[i] = data[i]
      this.updateData[i]['number'] = i + 1
      let newDate = this.getDate(data[i].date)
      this.updateData[i]['date'] = newDate
    }

    return this.updateData
  }

  getDate (date) {
    let newDate = new Date(date)
    let month = ''
    let date1 = ''
    if (newDate.getMonth() < 10) {
      month = '0' + (newDate.getMonth() + 1)
    } else {
      month = (newDate.getMonth() + 1)
    }
    if (newDate.getDate() < 10) {
      date1 = '0' + newDate.getDate()
    } else {
      date1 = newDate.getDate()
    }
    newDate = newDate.getFullYear() + '-' + month + '-' + date1

    return newDate
  }
}
